import React, { useEffect, useState } from 'react';
import {
  Button,
  VStack,
  Text,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
  useDisclosure,
} from '@chakra-ui/react';
import { useWebSocketContext } from '../../hooks/useBroadcastContext';

const SystemCheck = () => {
  const [humanTaskMsg, setHumanTaskMsg] = useState<string>('');
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const cancelRef = React.useRef<any>();
  const { registerEventHandler } = useWebSocketContext();

  useEffect(() => {
    const unregister = registerEventHandler((message) => {
      if (message.event === 'chrome_download') {
        setIsModalOpen(false);
      } else if (message.event === 'human_task') {
        if (!message.data) {
          return;
        }

        window.electron.ipcRenderer.sendMessage(
          'notification',
          '',
          '',
        );

        const data = message.data as {
          message: string;
          value: string;
          type: string;
        };

        if (data.type === 'strategy') {
          setHumanTaskMsg(data.message);
          onOpen();
        } else if (data.type === 'system') {
          setHumanTaskMsg(data.message);
          onOpen();
        }
      }
    });

    // 
    return () => unregister();
  }, [registerEventHandler]); // eslint-disable-line

  const confirmDownload = () => {
    window.electron.ipcRenderer.sendMessage(
      'open-url',
      'https://www.google.cn/chrome/',
    );
  };

  useEffect(() => {
    const version = window.electron.ipcRenderer.get('get-browser-version');
    if (!version) {
      setIsModalOpen(false);
    }
  }, []);

  return (
    <>
      <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader></ModalHeader>
          <ModalCloseButton />
          <ModalBody>
             Chrome  Chrome
            
          </ModalBody>
          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={confirmDownload}>
              
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>

      <AlertDialog
        isOpen={isOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClose}
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              
            </AlertDialogHeader>

            <AlertDialogBody>
              <VStack>
                <Text></Text>
                <Text>{humanTaskMsg}</Text>
                <Text>
                  
                </Text>
              </VStack>
            </AlertDialogBody>

            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>
                
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </>
  );
};

export default SystemCheck;
