import axios, {
  Method,
  InternalAxiosRequestConfig,
  AxiosResponse,
} from 'axios';

interface ConfigType {
  headers?: { [key: string]: string };
  hold?: boolean;
  timeout?: number;
}
interface ResponseDataType {
  code: number;
  message: string;
  data: any;
}

/**
 * 
 */
function requestStart(
  config: InternalAxiosRequestConfig,
): InternalAxiosRequestConfig {
  return config;
}

/**
 * ,
 */
function responseSuccess(response: AxiosResponse<ResponseDataType>) {
  return response;
}
/**
 * 
 */
function checkRes(data: ResponseDataType) {
  if (data === undefined) {
    return Promise.reject('');
  }
  if (data?.code && (data.code < 200 || data.code >= 400)) {
    return Promise.reject(data);
  }
  return data;
}

/**
 * 
 */
function responseError(err: any) {
  if (!err) {
    return Promise.reject({ message: '' });
  }

  // 
  if (err.message === 'Network Error') {
    return Promise.reject({ message: '' });
  }

  // 
  if (err.code === 'ECONNABORTED') {
    return Promise.reject({ message: '' });
  }

  // 
  if (err.response) {
    //  err.response.status 
    return Promise.reject(err.response.data);
  }

  // 
  return Promise.reject(err);
}

/*  */
const instance = axios.create({
  timeout: 60000, // 
  headers: {
    'content-type': 'application/json',
    'Cache-Control': 'no-cache',
  },
});

/*  */
instance.interceptors.request.use(requestStart, (err) => Promise.reject(err));
/*  */
instance.interceptors.response.use(responseSuccess, (err) =>
  Promise.reject(err),
);

export function request(
  url: string,
  data: any,
  config: ConfigType,
  method: Method,
): any {
  /*  */
  Object.keys(data).forEach((key) => {
    if (data[key] === null || data[key] === undefined) {
      delete data[key]; //  null  undefined
    }
  });

  return instance
    .request({
      baseURL: `http://127.0.0.1:${window.electron.getPort()}`,
      url,
      method,
      data: ['POST', 'PUT'].includes(method) ? data : null,
      params: !['POST', 'PUT'].includes(method) ? data : null,
      ...config, // custom config
    })
    .then((res) => checkRes(res.data))
    .catch((err) => responseError(err));
}

/**
 * api
 * @param {String} url
 * @param {Any} params
 * @param {Object} config
 * @returns
 */
export function GET<T = undefined>(
  url: string,
  params = {},
  config: ConfigType = {},
): Promise<T> {
  return request(url, params, config, 'GET');
}

export function POST<T = undefined>(
  url: string,
  data = {},
  config: ConfigType = {},
): Promise<T> {
  return request(url, data, config, 'POST');
}

export function PUT<T = undefined>(
  url: string,
  data = {},
  config: ConfigType = {},
): Promise<T> {
  return request(url, data, config, 'PUT');
}

export function DELETE<T = undefined>(
  url: string,
  data = {},
  config: ConfigType = {},
): Promise<T> {
  return request(url, data, config, 'DELETE');
}
